@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@400;500;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

html {
  font-size: 0.9375rem;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "League Spartan";
  font-weight: 400;
  margin: 0;
  padding: 4rem 1.5rem;
}

.content {
  display: grid;
  grid-template-areas: "header" "rating" "review" "footer";
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 61.25em) {
  .content {
    grid-template-columns: 40% 1fr;
    grid-template-areas: "header rating" "review review" "footer footer";
  }
}

.footer {
  grid-area: footer;
}
@media (min-width: 61.25em) {
  .footer {
    display: flex;
    justify-content: center;
    margin-top: 1.25rem;
  }
}

.header {
  text-align: center;
  grid-area: header;
}
.header .header__title {
  font-size: 2.5rem;
  color: #502050;
  margin: 0 1.375rem;
  line-height: 0.8;
}
.header .header__subs {
  font-size: 1.2rem;
  margin-top: 1.375rem;
}

.rt-container__ratings {
  text-align: center;
  margin-top: 1.5rem;
  grid-area: rating;
}

.rv__container {
  text-align: left;
  margin-top: 2.75rem;
  grid-area: review;
}

@media (min-width: 61.25em) {
  .header {
    text-align: left;
    padding-block: 1.5rem;
  }
  .header .header__title {
    font-size: 3.5rem;
    margin-block: 1.25rem;
    margin-inline: 0;
  }
  .header .header__subs {
    margin-block: 1.75rem;
  }
}
.rv__container {
  display: flex;
  flex-flow: column nowrap;
  gap: 1.25rem;
  color: white;
}
.rv__container .container__reviews {
  background-color: #502050;
  border-radius: 10px;
  padding-top: 2.5rem;
  padding-bottom: 2rem;
  padding-inline: 2rem;
}
.rv__container .container__reviews .reviews__profile {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  vertical-align: middle;
  margin-bottom: 1.75rem;
}
.rv__container .container__reviews .reviews__profile .profile__user {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}
.rv__container .container__reviews .reviews__profile .profile__info {
  margin-left: 1.5rem;
  margin-block: 1px;
}
.rv__container .container__reviews .reviews__profile .profile__info .info__username {
  font-size: 1rem;
  font-weight: 700;
  margin: 0;
}
.rv__container .container__reviews .reviews__profile .profile__info .info__status {
  color: #ee68a4;
}

@media (min-width: 40em) {
  .rv__container {
    flex-flow: row wrap;
    justify-content: center;
    gap: 1rem;
    margin-block: 1.5rem;
    padding: 0;
  }
  .rv__container .container__reviews {
    flex: 1 1 auto;
    max-width: 21.875rem;
  }
}
@media (min-width: 61.25em) {
  .rv__container {
    flex-flow: row nowrap;
    margin: 0;
  }
  .rv__container .container__reviews:nth-child(1) {
    transform: translateY(-1rem);
  }
  .rv__container .container__reviews:nth-child(3) {
    transform: translateY(1rem);
  }
}
.rt-container__ratings {
  display: flex;
  flex-flow: column nowrap;
  gap: 1.25rem;
  padding: 0;
}
.rt-container__ratings .rating {
  background-color: #f7f2f7;
  border-radius: 10px;
  padding: 0.9rem;
}
.rt-container__ratings .rating .rating__stars {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}
.rt-container__ratings .rating .rating__stars .star__img {
  max-width: 100%;
}
.rt-container__ratings .rating .rating__heading {
  font-size: 1.05rem;
  font-weight: 700;
  color: #502050;
}

@media (min-width: 40em) {
  .rt-container__ratings {
    flex-flow: row wrap;
    justify-content: center;
  }
  .rt-container__ratings .rating {
    flex: 1 1 auto;
    max-width: 25rem;
  }
}
@media (min-width: 61.25em) {
  .rt-container__ratings {
    flex-flow: column nowrap;
    margin-block: auto;
    margin-inline-start: auto;
  }
  .rt-container__ratings .rating {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    max-width: initial;
  }
  .rt-container__ratings .rating .rating__stars {
    margin-bottom: 0;
    margin-inline: 1.5rem;
  }
  .rating__heading {
    margin-inline: 1.5rem;
  }
  .rating__heading:nth-child(2) {
    margin-right: 1.25rem;
  }

  .rt-container__ratings .rating:nth-of-type(1) {
    transform: translateX(-4.5rem);
  }
  .rt-container__ratings .rating:nth-of-type(2) {
    transform: translateX(-2.5rem);
  }
}
.footer__attribution {
  font-size: 1rem;
  text-align: center;
}
.footer__attribution a {
  color: #3e52a3;
}
@media (max-width: 39.9375em) {
  .footer__attribution {
    display: none;
  }
}